/*--------------------------------------------------------------
# General
--------------------------------------------------------------*/
/*======= GOOGLE FONTS =======*/

:root {
  scroll-behavior: smooth;
  --header-height: 3rem;
  /*---------- colors ----------*/
  --primary-color: #5d5fef;
  --secondary-color: #7879f1;
  --tertiary-color: #a5a6f6;
  --text-color: #25282b;
  --text-color-alt: #52575c;
  --text-color-light: #fff;
  --bg-color: #fff;
  --info-color:#1e2a8a;
  --error-color:#7f1d1d;
  --warning-color:#78350f;
  /*----------font and typography ----------*/
  --body-font: "Montserrat", sans-serif;
  --biggest-font-size: 6rem;
  --h1-font-size: 1.5rem;
  --h2-font-size: 1.25rem;
  --h3-font-size: 1.75rem;
  --normal-font-size: 1rem;
  --small-font-size: 0.938rem;
  --smaller-font-size: 0.813rem;
  --smallest-font-size: 0.75rem;
  /*---------- font weight ----------*/
  --font-light: 400;
  --font-medium: 500;
  --font-semi-bold: 600;
  /*---------- margins ----------*/
  --mb-1: 0.5rem;
  --mb-2: 1rem;
  --mb-3: 1.5rem;
  --mb-4: 2rem;
  --mb-5: 2.5rem;
  --mb-6: 3rem;
  /*---------- base ----------*/
  --box-shadow-normal: 0px 13px 19px rgba(0, 0, 0, 0.07);
  --border-edge: 1px solid var(--primary-color);
  --border-radius-soft: 6px;
  /*======= RESPONSIVE =======*/
  @media screen and (max-width: 768px) {
       :root {
          --biggest-font-size: 6rem;
          --h1-font-size: 2.25rem;
          --h2-font-size: 1.5rem;
          --h3-font-size: 1.25rem;
          --normal-font-size: 1rem;
          --small-font-size: 0.875rem;
          --smaller-font-size: 0.813rem;
      }
  }

}

/*========== STYLE.CSS ==========*/
*{
  margin:0;
  padding:0;
  
}
body {
  /* min-height: 100vh; */
  font-family: "Montserrat", sans-serif !important;
  font-family: var(--body-font) !important;
  color: #25282b !important;
  color: var(--text-color) !important;
  background: #fff !important;
  background: var(--bg-color) !important;
}

a {
  color: #5d5fef;
  color: var(--primary-color);
  text-decoration: none !important;
}

p {
  font-family: "Montserrat", sans-serif;
  font-family: var(--body-font);
  font-size: 1rem;
  font-size: var(--normal-font-size);
}

a:hover {
  color: #7879f1;
  color: var(--secondary-color);
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Montserrat", sans-serif;
  font-family: var(--body-font);
}

.divider{
  border-top: 1px solid #f0f0f0;
  margin: 0px;
}
/*--------------------------------------------------------------
# Dahsboard Main
--------------------------------------------------------------*/
#main {
  margin-top: 60px;
  padding: 20px 30px;
  transition: all 0.3s;
  background-color: #F6F8FB;
}

@media (max-width: 1199px) {
  #main {
    padding: 20px;
  }
}


/*--------------------------------------------------------------
# Page Title
--------------------------------------------------------------*/
.pagetitle {
  margin-bottom: 10px;
}

.pagetitle h1 {
  font-size: 24px;
  margin-bottom: 0;
  font-weight: 600;
  color: #012970;
}

/*--------------------------------------------------------------
# Section
--------------------------------------------------------------*/
.section {
  padding: 25px !important;
}

/*--------------------------------------------------------------
# Override some default Bootstrap stylings
--------------------------------------------------------------*/
/* Dropdown menus */
.dropdown-menu {
  border-radius: 4px;
  padding: 10px 0;
  -webkit-animation-name: dropdown-animate;
  animation-name: dropdown-animate;
  -webkit-animation-duration: 0.2s;
  animation-duration: 0.2s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  border: 0;
  box-shadow: 0 5px 30px 0 rgba(82, 63, 105, 0.2);
}

.dropdown-menu .dropdown-header,
.dropdown-menu .dropdown-footer {
  text-align: center;
  font-size: 14px;
  padding: 10px 25px;
}

.dropdown-menu .dropdown-footer a {
  color: #444444;
  text-decoration: underline;
}

.dropdown-menu .dropdown-footer a:hover {
  text-decoration: none;
}

.dropdown-menu .dropdown-divider {
  color: #a5c5fe;
  margin: 0;
}

.dropdown-menu .dropdown-item {
  font-size: 14px;
  padding: 10px 15px;
  transition: 0.3s;
}

.dropdown-menu .dropdown-item i {
  margin-right: 10px;
  font-size: 18px;
  line-height: 0;
}

.dropdown-menu .dropdown-item:hover {
  background-color: #f6f9ff;
}

@media (min-width: 768px) {
  .dropdown-menu-arrow::before {
    content: "";
    width: 13px;
    height: 13px;
    background: #fff;
    position: absolute;
    top: -7px;
    right: 20px;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    border-top: 1px solid #eaedf1;
    border-left: 1px solid #eaedf1;
  }
}

@-webkit-keyframes dropdown-animate {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }

  0% {
    opacity: 0;
  }
}

@keyframes dropdown-animate {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }

  0% {
    opacity: 0;
  }
}

/* Light Backgrounds */
.bg-primary-light {
  background-color: #cfe2ff;
  border-color: #cfe2ff;
}

.bg-secondary-light {
  background-color: #e2e3e5;
  border-color: #e2e3e5;
}

.bg-success-light {
  background-color: #d1e7dd;
  border-color: #d1e7dd;
}

.bg-danger-light {
  background-color: #f8d7da;
  border-color: #f8d7da;
}

.bg-warning-light {
  background-color: #fff3cd;
  border-color: #fff3cd;
}

.bg-info-light {
  background-color: #cff4fc;
  border-color: #cff4fc;
}

.bg-dark-light {
  background-color: #d3d3d4;
  border-color: #d3d3d4;
}

/* Card */
.card {
  margin-bottom: 20px;
  border: none;
  border-radius: 10px;
  box-shadow: 0px 0 30px rgba(1, 41, 112, 0.1);
}

.card-header,
.card-footer {
  border-color: #ebeef4;
  background-color: #fff;
  color: #798eb3;
  padding: 15px;
}

.card-title {
  padding: 20px 0 15px 0;
  font-size: 26px;
  font-weight: 700;
  color: #25282b;
  color: var(--text-color);
  font-family: "Montserrat", sans-serif;
}

.card-title span {
  color: #899bbd;
  font-size: 14px;
  font-weight: 400;
}

.card-body {
  padding: 50px 20px 40px 20px;
  border-radius: 10px;
  background-color: #fff;;
  box-shadow: 0px 7px 23px rgba(0, 0, 0, 0.05);
}

.card-img-overlay {
  background-color: rgba(255, 255, 255, 0.6);
}

/* Alerts */
.alert-heading {
  font-weight: 500;
  font-family: "Montserrat", sans-serif;
  font-size: 20px;
}

/* Close Button */
.btn-close {
  background-size: 25%;
}

.btn-close:focus {
  outline: 0;
  box-shadow: none;
}

/* Accordion */
.accordion-item {
  border: 1px solid #ebeef4;
}

.accordion-button:focus {
  outline: 0;
  box-shadow: none;
}

.accordion-button:not(.collapsed) {
  color: #012970;
  background-color: #f6f9ff;
}

.accordion-flush .accordion-button {
  padding: 15px 0;
  background: none;
  border: 0;
}

.accordion-flush .accordion-button:not(.collapsed) {
  box-shadow: none;
  color: #4154f1;
}

.accordion-flush .accordion-body {
  padding: 0 0 15px 0;
  color: #3e4f6f;
  font-size: 15px;
}

/* Breadcrumbs */
.breadcrumb {
  font-size: 14px !important;
  font-family: "Montserrat", sans-serif;
  color: #899bbd;
  font-weight: 600;
}

.breadcrumb a {
  color: #899bbd;
  transition: 0.3s;
  text-decoration: none;
}

.breadcrumb a:hover {
  color: #51678f;
}

.breadcrumb .breadcrumb-item::before {
  color: #899bbd;
}

.breadcrumb .active {
  color: #5d5fef;
  color: var(--primary-color);
  font-weight: 600;
}

/* Bordered Tabs */
.nav-tabs-bordered {
  border-bottom: 2px solid #ebeef4;
}

.nav-tabs-bordered .nav-link {
  margin-bottom: -2px;
  border: none;
  color: #2c384e;
}

.nav-tabs-bordered .nav-link:hover,
.nav-tabs-bordered .nav-link:focus {
  color: #4154f1;
}

.nav-tabs-bordered .nav-link.active {
  background-color: white;
  color: #4154f1;
  border-bottom: 2px solid #4154f1;
}

.breadcrumb-item.active {
  color: #5d5fef!important;
  color: var(--primary-color)!important;
}

/*--------------------------------------------------------------
# Landing Page Header
--------------------------------------------------------------*/

#header {
  background: #fff;
  background: var(--bg-color);
  transition: all 0.5s;
  z-index: 997;
  padding: 15px 0;
  top: 40px;
  box-shadow: 0px 5px 10px rgb(0 0 0 / 5%);
}

#header .header-scrolled {
  top: 0;
}

#header .logo {
  font-size: 1.5rem;
  font-size: var(--h1-font-size);
  margin: 0;
  padding: 0;
  line-height: 1;
  font-weight: 700;
  letter-spacing: 0.5px;
  font-family: "Montserrat", sans-serif;
  font-family: var(--body-font);
}

#header .logo a {
  color: #25282b;
  color: var(--text-color);
}

#header .logo img {
  max-height: 40px;
}

/*--------------------------------------------------------------
# Sidebar
--------------------------------------------------------------*/
.sidebar {
  position: fixed;
  top: 0px;
  left: 0;
  bottom: 0;
  width: 300px;
  z-index: 996;
  transition: all 0.3s;
  padding: 20px;
  overflow-y: auto;
  scrollbar-width: thin;
  scrollbar-color: #aab7cf transparent;
  box-shadow: 0px 0px 20px rgba(1, 41, 112, 0.1);
  background-color:white;
}

@media (max-width: 1199px) {
  .sidebar {
    left: -300px;
  }
}

.sidebar::-webkit-scrollbar {
  width: 5px;
  height: 8px;
  background-color: #fff;
}

.sidebar::-webkit-scrollbar-thumb {
  background-color: #aab7cf;
}

@media (min-width: 1200px) {

  #main{
    margin-left: 300px;
  }
}

@media (max-width: 1199px) {
  .toggle-sidebar .sidebar {
    left: 0;
  }
}

@media (min-width: 1200px) {

  .toggle-sidebar #main,
  .toggle-sidebar #footer {
    margin-left: 0;
  }

  .toggle-sidebar .sidebar {
    left: -300px;
  }
}

.sidebar-nav {
  padding: 0;
  margin: 0;
  list-style: none;
}

.sidebar-nav li {
  padding: 0;
  margin: 0;
  list-style: none;
}

.sidebar-nav .nav-item {
  margin-bottom: 5px;
}

.sidebar-nav .nav-heading {
  font-size: 11px;
  text-transform: uppercase;
  color: #899bbd;
  font-weight: 600;
  margin: 10px 0 5px 15px;
}

.sidebar-nav .nav-link {
  display: flex;
  align-items: center;
  font-size: 14px;
  font-weight: 700;
  color: #5d5fef;
  color: var(--primary-color);
  transition: 0.3;
  background: #f6f9ff;
  padding: 14px 15px;
  border-radius: 4px;
}

.sidebar-nav .nav-link i {
  font-size: 16px;
  margin-right: 10px;
  color: #5d5fef;
  color: var(--primary-color);
}

/* inactive state */
.sidebar-nav .nav-link.collapsed {
  color: #25282b;
  color: var(--text-color);
  background: white;
  font-weight: 700;
}

.sidebar-nav .nav-link.collapsed:hover {
  background: #f6f9ff;
  color: #5d5fef;
  color: var(--primary-color);
}
/* 
.nav-link:focus, .nav-link:hover{
  background-color: ;
} */  

.sidebar-nav .nav-link.collapsed i {
  color: #899bbd;
}

.sidebar-nav .nav-link a:hover,
.sidebar-nav .nav-link a.active {
  color: #4154f1;
}

.sidebar-nav .nav-link a.active i {
  background-color: #4154f1;
}


.sidebar-nav .nav-link:hover i {
  color: #5d5fef;
  color: var(--primary-color);
}

.sidebar-nav .nav-link .fa-chevron-down {
  margin-left: 62px;
  transition: -webkit-transform 0.2s ease-in-out;
  transition: transform 0.2s ease-in-out;
  transition: transform 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
}

.sidebar-nav .nav-link:not(.collapsed) .fa-chevron-down {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}

.sidebar-nav .nav-content {
  padding: 5px 0 0 0;
  margin: 0;
  list-style: none;
}

.sidebar-nav .nav-content a {
  display: flex;
  align-items: center;
  font-size: 14px;
  font-weight: 600;
  color: #25282b;
  color: var(--text-color);
  transition: 0.3;
  padding: 10px 0 10px 40px;
  transition: 0.3s;
}

.sidebar-nav .nav-content a i {
  font-size: 6px;
  margin-right: 8px;
  line-height: 0;
  border-radius: 50%;
}

.sidebar-nav .nav-content a:hover,
.sidebar-nav .nav-content a.active {
  color: #5d5fef;
  color: var(--primary-color);
}

.sidebar-nav .nav-content a.active i {
  background-color: #4154f1;
}

.sidebar h4{
  padding: 10px;
