  background: #EBF9F1;
  border-radius: 22px;
  align-items: center;
  padding: 6px 12px;
  grid-gap: 10px;
  gap: 10px;
}

.appt_status[data-status="Cancelled"]:after{
  content: "Cancelled";
  color: #fff;
  background: #DC2626;
  border-radius: 22px;
  padding: 6px 12px;
  grid-gap: 10px;
  gap: 10px;
}

.appt_status[data-status="Finished"]:after{
  content: "Finished";
  color: #fff;
  background: #1F9254;
  border-radius: 22px;
  padding: 6px 12px;
  grid-gap: 10px;
  gap: 10px;
}

